








import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import { formatDate } from '@/until/index'

@Component({
  components: {
    HelloWorld
  },
  filters: {
    getFormatDate (date: any) {
      return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号'
    }
  }
})
export default class Home extends Vue {
  $createDialog : any
  $createToast : any
  noticeId = ''
  houseId = ''
  page = 1
  isShowMore = true
  noticeList = []
  listData = []
  detailData={}

  activated () {
    // this.getWeiXinConfig()
    this.pageInit()
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log(result)
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  getBindingInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据获取中...'
      })
      toast.show()
      this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          } else if (response.data.sessionId) {
            this.$store.commit('SET_SESSION_ID', response.data.sessionId)
            this.$store.commit('SET_USER_INFO', response.data.userInfo)
            this.getNoticeById()
          }
        }
        toast.hide()
      })
    } else {
      console.log('不是微信登陆')
    }
  }
  pageInit () {
    let query = this.$route.query
    if (query.Id) {
      this.getDetailData(query.Id)
    } else if (query.code) {
      let state = query.state
      if (state) {
        let stateArr = state.split('@')
        if (stateArr[0] === 'noticeId') {
          this.noticeId = stateArr[1]
          this.getBindingInfo()
        }
      }
    }
  }
  getDetailData (id:any) {
    //获取详情数据
    this.detailData=id
  }
  getNoticeById () {
    this.$store.dispatch('getNoticeById', {id: this.noticeId}).then((response) => {
      if (response.success) {
        this.detailData = response.data
      }
    })
  }
  getHouseList () {
    this.$store.dispatch('getHouseList', {}).then((response) => {
      if (response.data.list) {
        this.listData = response.data.list
        if (!this.houseId) {
          this.houseId = this.listData[0].id
        }
        this.$store.commit('SET_HOUSE_LIST', this.listData)
        this.noticeList = []
        this.page = 1
        this.getNoticeList()
      }
    })
  }

  get houseOptions () {
    this.$store.state.houseList.forEach((obj) =>{
      obj.value = obj.id
      obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' '+ obj.unitNum + '单元' + obj.floorNum + '层' + obj.roomNum + '室'
    })
    return this.$store.state.houseList
  }
  get userInfo () {
    return this.$store.state.userInfo
  }

  getHouseInfo () {
    let info = {}
    this.$store.state.houseList.forEach((obj) =>{
      if (obj.id === this.houseId) {
        info = obj
      }
    })
    return info
  }

  getNoticeList () {
    let houseInfo = this.getHouseInfo()
    console.log(houseInfo, 'houseInfo')
    const toast = this['$createToast']({
      time: 0,
      txt: '数据获取中...'
    })
    toast.show()
    this.$store.dispatch('getNoticeList', { page: this.page, accountId: houseInfo.accountId }).then((response) => {
      if (response.success) {
          if (response.data.list.length > 0) {
            this.noticeList = this.noticeList.concat(response.data.list)
          } else {
            this.isShowMore = false
          }
      }
      toast.hide()
    })
  }

  houseChange () {
    this.page = 1
    this.noticeList = []
    this.getNoticeList()
  }

  showMoreClick () {
    this.page++
    this.getNoticeList()
  }

}
